#contact {
  width: 150%;
  position: absolute;
  left: -25%;
  background: -webkit-linear-gradient(45deg, var(--blue-color), var(--purple-color));
  background: -o-linear-gradient(45deg, var(--blue-color), var(--purple-color));
  background: linear-gradient(45deg, var(--blue-color), var(--purple-color));
  border-top-left-radius: 50%;
  border-top-right-radius: 50%;
}

#contact-container {
  max-width: 1000px;
  margin: 0 auto;
}

#contact-container h1 {
  font-weight: 400;
  font-size: 40px;
  color: white;
  margin-top: 60px;
  text-align: center;
}

#contact-box {
  text-align: center;
  font-size: 40px;
  color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin: 80px 0 80px 0;
  -ms-flex-line-pack: start;
      align-content: flex-start;
}

#phone, #email {
  width: 100%;
  height: 100px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}

#contact-box span {
  margin-left: 20px;
}



